import VueRouter from 'vue-router'

import HomePage from './page/home.vue'
import WaterCost from './page/waterCost.vue'
import PowerCost from './page/powerCost.vue'
import GasCost from './page/gasCost.vue'
import RentCost from './page/rentCost.vue'
import CarbonNeutral from './page/carbonNeutral.vue'
import Finance from './page/finance.vue'
import Monitoring from './page/monitoring.vue'


var router = new VueRouter({
    routes: [
        {
            path: '/',
            redirect: '/home'
        },
        {
            path: '/home',
            meta: {
                index: 1,
                title: '首页',
            },
            component: HomePage
        },
        {
            path: '/waterCost',
            meta: {
                index: 2,
                title: '水费',
            },
            component: WaterCost
        },
        {
            path: '/powerCost',
            meta: {
                index: 3,
                title: '电费',
            },
            component: PowerCost
        },
        {
            path: '/gasCost',
            meta: {
                index: 4,
                title: '燃气费',
            },
            component: GasCost
        },
        {
            path: '/rentCost',
            meta: {
                index: 5,
                title: '房租',
            },
            component: RentCost
        },
        {
            path: '/carbonNeutral',
            meta: {
                index: 6,
                title: '碳中和',
            },
            component: CarbonNeutral
        },
        {
            path: '/finance',
            meta: {
                index: 7,
                title: '财务指标',
            },
            component: Finance
        },
        {
            path: '/monitoring',
            meta: {
                index: 8,
                title: '监控中心',
            },
            component: Monitoring
        },
    ]
})

export default router;